.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 114px;
  padding-top: 10px;
  margin: auto;
}
.header a {
  text-decoration: none;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-menu {
  display: flex;
  margin-right: 36px;
}
.header-menu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 43px;
  padding-left: 0;
}
.header-menu::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.2);
  transform: translate(18px, 13px);
}

.header-phones {
  font-weight: 600;
  color: var(--font-black);
}

.header-search {
  cursor: pointer;
}